.eos-recarge-wrap{
	position: fixed;
	width:100%;
	height:100%;
	background:rgba(0,0,0,.7);
	top:0;
	left:0;
	z-index: 999;
}
.eos-recarge-wrap .eos-box{
	width:90%;
	max-width: 800px;
	margin:1rem auto;
	background: #fff;
	border-radius: .3rem;
	padding:.3rem;
	font-size:.6rem;
}
.label{
	background:rgba(35,45,75,1);
	color:#C6CBDB;
	width:70%;
}
.eos-recarge-wrap  .eos-box .title{
	text-align:center;
	font-size:.6rem;
	margin:0 0 .8rem
}
.eos-recarge-wrap .eos-box .input-box{
	font-size:.4rem;
	font-weight: 600;
	margin:.3rem 0;
}
.eos-recarge-wrap .eos-box .input-box input{
	width: 80%;
    border: 0;
    border-bottom: .02rem solid #E1E1E1;
}
.eos-recarge-wrap .eos-box .copy-btn{
	width:100%;
}
input::-moz-placeholder{
	color:#999999;
	font-size:.4rem;
}
.eos-recarge-wrap .eos-box .tip{
	color:red;
	font-size:.3rem;
	margin:.3rem 0;
	text-align:center;
	
}
.eos-recarge-wrap .eos-box .canle{
	text-align:right;
}
.eos-recarge-wrap .eos-box .canle>img{
	width:.56rem;
	height:.56rem;
}
