.eos-recarge-wrap{
	position: fixed;
	width:100%;
	height:100%;
	background:rgba(0,0,0,.7);
	top:0;
	left:0;
	z-index: 999;
	
}
.eos-recarge-wrap .eos-box{
	width:90%;
	max-width:800px;
	margin:1rem auto;
	background: #fff;
	border-radius: .3rem;
	padding:.3rem;
	font-size:.6rem;
}
.eos-recarge-wrap .eos-box .input-box{
	font-size:.6rem;
	font-weight: 600;
}
.eos-recarge-wrap .eos-box .input-box input{
	width: 80%;
    border: 0;
    border-bottom: .02rem solid #E1E1E1;
}
.eos-recarge-wrap .eos-box .btn{
	width: 40%;
    height: 1rem;
    border: 0;
    background: rgba(69,81,117,1);
    color: #fff;
    font-size: .4rem;
    line-height: 1rem;
    padding: 0;
}
input::-moz-placeholder{
	color:#999999;
	font-size:.4rem;
}
.eos-recarge-wrap .eos-box .eos-tip{
	color:red;
	font-size:.44rem;
	margin:.3rem 0
	
}
.eos-recarge-wrap .eos-box .canle{
	text-align:right;
}
.eos-recarge-wrap .eos-box .canle>img{
	width:.56rem;
	height:.56rem;
}
