.menu-wrap{
	width:6.04rem;
	box-sizing: border-box;
	padding:.3rem;
	border-radius: .15rem;
	background: #FFFFFF;
	position: absolute;
	right:.3rem;
	top:1rem;
}
.menu-wrap .menu-login{
	font-size:.32rem;
	color:black;
	font-weight: 600;
	line-height:1.4rem;
}
.menu-wrap .menu-login .img-box{
	width:1.4rem;
	height:1.4rem;
	overflow: hidden;
	border-radius: 50%;
	margin-right:.15rem;
}
.menu-wrap .menu-login .img-box>img{
	width:100%;
	min-height: 100%;
}
.menu-wrap .menu-item{
	font-size:.3rem;
	border-bottom: .02rem solid #e1e1e1;
	box-sizing: border-box;
	padding:.2rem 0;
}
.menu-wrap .menu-item .img-box{
	width:.6rem;
	height:.6rem;
	overflow: hidden;
	margin-top:.2rem;

}
.menu-wrap .menu-item span{
	padding:0 .3rem;
	font-size:.4rem;
	line-height: .9rem;
}
.menu-wrap .menu-item .right{
	color:#FF5858;
	font-size: .5rem;
}
.menu-wrap .gl{
	width: .26rem;
    height: .44rem;
    margin-top: .3rem;
}
.menu-wrap .isgl .img-box{
	width:1rem;
	height:1rem;
}
.menu-wrap .isgl span{
	line-height: 1rem;
	font-size: .5rem;
}

