.room-list-wrap{
	width:100%;
	height:1.8rem;
	overflow: hidden;
	margin-bottom: .15rem;
	position:relative;
}
.room-list-wrap .room-list-bg{
	width:100%;
	height: 100%;
}
.room-list-wrap .room-content-box{
	position: absolute;
    top: 0;
    left:0;
    width: 90%;
	color:#fff;
}
.room-list-wrap .room-content-box .user-img{
	width:.25rem;
	height:.25rem;
}
.room-list-wrap .room-content-box .room-content-item{
	font-size:.38rem;
	margin:.3rem 0 .15rem;
} 
.room-list-wrap .label{
	background: #fff;
    margin-left: .8rem;
    box-shadow: 0 0 .1rem .06rem #D774FB;
}
.room-list-wrap .label-1{
	color:#9349F5;
}
.room-list-wrap .label-2{
	color:#8c3163;
}
.room-list-wrap .label-3{
	color:#976743;
}
.room-list-wrap .label-4{
	color:#95283d;
}


